// import config from '@/axios/config';
import config from '../axios/config';
import axios from 'axios';

export async function uploadData(formData) {
    return new Promise((reslove, reject) => {
        axios({
            method: "post",
            url: `${config.url.uploadData}`,
            config: {
                "headers": {
                    'Content-Type': 'multipart/form-data' //application/json; charset=utf-8
                },
            },
            data: formData
        }).then((response) => {
            if (response.status >= 400) {
                reject();
            } else if (response.status === 200 || response.status === 201 || response.status === 204) {
                reslove(response.data);
            } else {
                reject();
            }
        }).catch(error => {
            reject();
            //  message.error('error message');
        })
    })
}

export async function uploadVideo(formData) {
    return new Promise((reslove, reject) => {
        axios({
            method: "post",
            url: `${config.url.uploadVideo}`,
            config: {
                "headers": {
                    'Content-Type': 'multipart/form-data' //application/json; charset=utf-8
                },
            },
            data: formData,
            onUploadProgress :(progressEvent)=>{
                //F12展示进度情况
                console.log(progressEvent);
                console.log(Math.round(progressEvent.loaded / progressEvent.total * 100) + '%');
                //定义一个percent
                let percent = (Math.round(progressEvent.loaded / progressEvent.total * 100))
                console.log(percent);
                this.setState({percent:percent})
                // this.setState({progressEvent:(Math.round(progressEvent.loaded / progressEvent.total * 100))});
            }
        }).then((response) => {
            if (response.status >= 400) {
                reject();
            } else if (response.status === 200 || response.status === 201 || response.status === 204) {
                reslove(response.data);
            } else {
                reject();
            }
        }).catch(error => {
            reject();
            //  message.error('error message');
        })
    })
}


export function generate(url, data) {
    return axios({
        method: "post",
        url: url,
        config: {
            "headers": {
                'Content-Type': 'application/json; charset=utf-8'
            },
        },
        data: data
    })
}

export function factScoring(filename, fact, method) {
    return axios({
        "method": "POST",
        "url": config.url.factScoring,
        "headers": {
            "Content-Type": "application/json; charset=utf-8"
        },
        "data": {
            "file_name": filename,
            "fact": fact,
            "method": method,
        }
    })
}

export async function fetchStory(id) {
    return axios({
        method: "get",
        "url": `${config.url.fetch}/${id}.json`,
        config: {
            "headers": {
                'Content-Type': 'application/json; charset=utf-8'
            },
        }
    })
}

export async function share(uuid) {
    return axios({
        method: "post",
        url: `${config.url.share}`,
        config: {
            "headers": {
                'Content-Type': 'application/json; charset=utf-8'
            },
        },
        data: {
            "uuid": uuid
        }
    })
}


export async function getReport(params) {
    return axios({
        method: "get",
        url: `${config.url.getReport}`,
        params: params
    })
}

export async function deleteReport(uuid, page, per_page) {
    return axios({
        method: "delete",
        url: `${config.url.delete}`,
        params: {
            uuid: uuid,
            page: page,
            per_page: per_page
        }
    })
}

/*****
 *技术栈：
 ssr(后端渲染)，koa(koa-views)：模板渲染，渲染出要生成pdf的页面)
 node.js 生成pdf插件 puppeteer：将渲染的页面生成PDF
 koa-send：将生成的PDF返回给前端页面下载 
 * 
 data是后端用于渲染页面的数据
 */
export function generatePDF(data) {
    return axios({
        method: 'post',
        url: `/generatePDF`,
        param: {},
        data: data
    })
}
